import * as React from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete, { createFilterOptions } from '@mui/material/Autocomplete';
import productModel from '../../../Modules/productModel';
import axios from 'axios';
import config from "../../../Configs/Configs"
import  "./AutoComplete.css"
const filter = createFilterOptions<FilmOptionType>();

export default function AutoComplete() {
  const [value, setValue] = React.useState<productModel | null>(null);
  const [products,setProducts] = React.useState<productModel[] | null >()
  React.useEffect(()=>{
      axios.get(config.urls.product).then((res)=>{setProducts(res.data)}).
      catch((err)=>{return err})
  })

  return (
    <Autocomplete
    
      selectOnFocus
      clearOnBlur
      handleHomeEndKeys
      id="free-solo-with-text-demo"
      options={products}
      getOptionLabel={(option) => {
        // Value selected with enter, right from the input
        if (typeof option === 'string') {
          return option;
        }
        
        // Regular option
        return option.name;
      }}
      renderOption={(props, option) => <div className ="RenderOption"><a href ={"/products/buy/"+option.id + "&&" + option.image}>{option.name}</a></div>}
      sx={{ width: 700 }}
      freeSolo
      renderInput={(params) => (
        <TextField {...params} label="חיפוש" variant="filled" />
      )}
    />
  );
}

interface FilmOptionType {
  inputValue?: productModel;
  name: string;
}

